<template>
    <div class="footer" v-show="total">
        <input type="checkbox" v-model="allChecked" @click="checkAll">&nbsp;
        <span>已完成 {{dones}} / 总共 {{total}} </span>
        <button @click="clearDones">清除已完成</button>
    </div>
</template>

<script>
    export default {
        name: "MyFooter",
        props: ['todos','checkAll','clearDones'],
        // props: ['todos','clearDones'],
        // methods: {
        //     checkAll(value) {
        //         this.$emit('checkAll',value);
        //     }
        // },
        computed: {
            dones() {
                //方法一
                // let sum = 0;
                // this.todos.map(item => { if(item.done) sum++; });
                // return sum;
                //方法二
                // return this.todos.reduce((sum, todo) => sum + (todo.done ? 1 : 0),0);
                //方法三
                return this.todos.filter(todo=>todo.done).length;
            },
            total(){
                return this.todos.length;
            },
            allChecked:{
                get(){
                    return this.total > 0 && this.total === this.dones;
                },
                set(value){
                    this.checkAll(value);
                    // this.$emit('checkAll',value);
                }
            }
        },
    }
</script>

<style lang="less" scoped>
    .footer {
        border:1px solid lightgray;
        border-top: 2px dashed whitesmoke;  /*上部边框样式*/
        border-radius: 0px 0px 20px 20px;
        text-align: left;
        font-size: 16px;
        width: 300px; /*固定宽度*/
        margin: 0 auto; /*居中*/
        margin-top: -1px;
        padding: 10px;
        padding-bottom: 14px;
        span {
            vertical-align: top;
        }
        button{
            background-color: #ffc0cb;
            font-size: 14px;
            width: 90px;
            height: 25px;
            border: 0;
            border-radius: 12px;
            float:right;
            outline: none;
        }
        button:hover {
            color: white;
            background-color: palevioletred;
            //background: linear-gradient(225deg, #ec5fec, #d63e57);
            box-shadow: -3px 3px 3px #cac6c6, 3px -3px 3px #fcf6f6;
        }
        input[type="checkbox"] {
            width: 1.3em;
            height: 1.3em;
        }
    }
</style>