<template>
  <div class="todo-list">
    <h2>Todo List</h2>
    <MyHeader :addTodo="addTodo" />
    <MyList
      :todos="todos"
      :checkTodo="checkTodo"
      :updateTodo="updateTodo"
      :deleteTodo="deleteTodo"
    />
    <MyFooter :todos="todos" :checkAll="checkAll" :clearDones="clearDones" />
  </div>
</template>

<script>
import { useStore } from "vuex"
import MyHeader from "./MyHeader.vue"
import MyFooter from "./MyFooter.vue"
import MyList from "./MyList.vue"

export default {
  name: "TodoList",
  components: {
    MyHeader,
    MyFooter,
    MyList,
  },
  data() {
    return {
      //读取浏览器本地存储，如果数据为空则生成初始数据
      todos: JSON.parse(localStorage.getItem("todos")) || [
        { id: "001", title: "抽烟", done: true },
        { id: "002", title: "喝酒", done: false },
        { id: "003", title: "开车", done: false },
        { id: "004", title: "打牌", done: true },
      ],
      store: useStore(),
    }
  },
  methods: {
    addTodo(todoObj) {
      // this.todos.unshift(todoObj)
      // this.$set(todoObj, "isShow", true);
      this.todos.unshift(todoObj)
    },
    checkTodo(id, checked) {
      this.todos.map((todo) => {
        if (todo.id === id) todo.done = checked
      })
    },
    updateTodo(id, value) {
      this.todos.map((todo) => {
        if (todo.id === id) todo.title = value
      })
    },
    deleteTodo(id) {
      this.todos = this.todos.filter((todo) => todo.id !== id)
    },
    checkAll(value) {
      this.todos.map((todo) => {
        todo.done = value
      })
    },
    clearDones() {
      if (confirm("确定删除全部已完成吗？")) {
        this.todos = this.todos.filter((todo) => !todo.done)
      }
    },
  },
  watch: {
    todos: {
      deep: true, // 开启深度监视，否则勾选的状态监测不到
      immediate: true, // 该回调将会在侦听开始之后被立即调用
      handler(value) {
        value = value.filter(
          (todo) => todo && todo.id && todo.title && todo.done !== undefined
        ) //过滤掉空数据
        localStorage.setItem("todos", JSON.stringify(value)) //将数据写入浏览器本地存储
        // console.log("state.todes Before:", this.store.state.todos)
        this.store.commit("setTodos", this.todos)
        // console.log("state.todes After:", this.store.state.todos)
      },
    },
  },
  // mounted() {
  //   this.$bus.$on("checkTodo", this.checkTodo)
  //   this.$bus.$on("updateTodo", this.updateTodo)
  //   this.$bus.$on("deleteTodo", this.deleteTodo)
  // },
  // beforeUnmount() {
  //   this.$bus.$off(["checkTodo", "updateTodo", "deleteTodo"])
  // },
}
</script>

<style lang="less" scoped>
.todo-list {
  box-shadow: 1px 1px 6px rgb(32 33 36 / 28%);
  border: 1px solid lightgray;
  border-radius: 30px;
  text-align: left;
  list-style-type: none;
  padding-bottom: 30px;
  font-size: 18px;
  width: 340px; /*固定宽度*/
  margin: 0 auto; /*居中*/
  h2 {
    text-align: center;
    margin: 0;
    color: lightgray;
  }
}
</style>
