<template>
  <ul class="todo-main" v-show="todos.length">
    <input
      type="text"
      ref="inputSearch"
      placeholder="输入搜索内容"
      v-model="searchText"
    />
    <button @click="regSearch">正则搜索</button>
    <transition-group
      appear
      name="animate__animated animate__bounce"
      enter-active-class="animate__fadeInLeft"
      leave-active-class="animate__fadeOutRight"
      :duration="{ enter: 800, leave: 800 }"
    >
      <MyItem
        v-for="todoObj in todos"
        v-show="isShow(todoObj)"
        :key="todoObj.id"
        :todo="todoObj"
        :checkTodo="checkTodo"
        :updateTodo="updateTodo"
        :deleteTodo="deleteTodo"
      />
    </transition-group>
  </ul>
</template>

<script>
import MyItem from "./MyItem.vue"
import "animate.css" //引入CSS动画库
export default {
  name: "MyList",
  data() {
    return {
      regex: /(?:)/,
      regexMatch: false,
      searchText: "",
      keyword: "",
      timer: null,
    }
  },
  methods: {
    regSearch() {
      //this.regex = this.regex+'' === /\S/+'' ? /[0-9A-Za-z]/:/\S/;
      clearTimeout(this.timer)
      this.regex = new RegExp(this.$refs.inputSearch.value, "i")
      this.regexMatch = true //开启正则匹配模式
    },
    isShow(todo) {
      let normalMatch = todo.title.toLowerCase().indexOf(this.keyword) > -1
      return normalMatch || (this.regexMatch && this.regex.test(todo.title))
    },
  },
  watch: {
    searchText() {
      // 防抖式更新搜索关键词
      // 如果定时器存在就默认清除一次
      clearTimeout(this.timer)
      // 开启定时器
      this.timer = setTimeout(() => {
        this.keyword = this.searchText
        this.regexMatch = false //关闭正则匹配模式
      }, 600)
    },
  },
  props: ["todos", "checkTodo", "updateTodo", "deleteTodo"],
  components: { MyItem },
}
</script>

<style scoped>
.todo-main {
  border: 1px solid lightgray;
  border-bottom: none; /*底部边框不显示*/
  border-radius: 20px 20px 0px 0px;
  text-align: left;
  list-style-type: none; /*去除圆点*/
  padding: 10px;
  font-size: 16px;
  width: 300px; /*固定宽度*/
  margin: 0 auto; /*居中*/
  max-height: 400px;
  overflow-y: auto;
  overflow-x: hidden; /*防止动画过程中出现水平滚动条*/
}

.todo-main > input {
  border: 1px solid lightgray;
  border-radius: 16px;
  font-size: 16px;
  color: purple;
  margin-left: 8px;
  padding: 2px 15px 2px 15px;
  width: 150px;
  outline: none;
}
.todo-main > input:hover {
  box-shadow: 0 1px 6px rgb(32 33 36 / 28%);
  border-color: rgba(223, 225, 229, 0);
}
.todo-main > button {
  background-color: #6dd5ed;
  margin: 4px 12px 10px 12px;
  font-size: 14px;
  width: 70px;
  height: 24px;
  border: 0;
  border-radius: 16px;
  position: relative;
  margin-left: 10px;
  outline: none;
}
.todo-main > button:hover {
  color: white;
  background: linear-gradient(225deg, #2193b0, #6dd5ed);
  box-shadow: -3px 3px 3px #cac6c6, 3px -3px 3px #fcf6f6;
}
</style>
