<template>
  <li>
    <label class="container">
      <!-- 如下代码也能实现功能，但是不太推荐，因为有点违反原则，修改了props -->
      <!-- <input type="checkbox"v-model="todo.done"/> -->
      <input
        type="checkbox"
        :checked="todo.done"
        @click="handleCheckTodo($event, todo.id)"
      />
      <div class="checkmark"></div>
    </label>
    &nbsp;
    <span v-show="!todo.isEdit" @click="handleEditTodo(todo)">
      {{ todo.title.slice(0, 30) + (todo.title.length > 30 ? " ..." : "") }}
      <!-- {{ todo.title }} -->
    </span>
    <input
      type="text"
      v-show="todo.isEdit"
      :value="todo.title"
      @blur="handleBlur($event, todo)"
      @keyup.enter="handleBlur($event, todo)"
      ref="inputbox"
    />
    <button @click="handleDeleteTodo(todo.id)">删除</button>
  </li>
</template>

<script>
export default {
  name: "MyItem",
  props: ["todo", "checkTodo", "updateTodo", "deleteTodo"],
  methods: {
    handleCheckTodo(e, id) {
      this.checkTodo(id, e.target.checked)
    },
    handleEditTodo(todo) {
      // if (!todo.isEdit == undefined) {
      //   todo.isEdit = true
      // } else {
      //   this.$set(todo, "isEdit", true)
      // }
      todo.isEdit = true
      this.$nextTick(function () {
        this.$refs.inputbox.focus()
      })
      // setTimeout(() => {
      //     this.$refs.inputbox.focus()
      // });//不给时间，虽然立即执行，但也会推向队列去执行，也能实现延后的效果
    },
    handleBlur(e, todo) {
      todo.isEdit = false
      if (!e.target.value.trim()) return alert("输入不能为空!")
      this.updateTodo(todo.id, e.target.value)
    },
    handleDeleteTodo(id) {
      //if(confirm("确定删除吗？")){
      this.deleteTodo(id)
      //}
    },
  },
}
</script>

<style lang="less" scoped>
li {
  background-color: whitesmoke;
  padding: 8px;
  font-size: 16px;
  margin: 5px;
  border-radius: 10px;
  clear: right;
  overflow: auto;
  span {
    vertical-align: 15%; // 调整竖向对齐
  }
  input {
    border: 1px solid lightgray;
    border-radius: 5px;
    font-size: 16px;
    padding: 1px;
    width: 150px;
    vertical-align: 15%;
    outline: none;
  }
  button {
    background-color: lightgray;
    font-size: 14px;
    width: 54px;
    height: 24px;
    border: 0;
    border-radius: 12px;
    float: right;
    outline: none;
    // display: none;
    // position: relative;
    // margin-left: 150px;
    // padding: 4px 12px 4px 12px;
  }
}
li:hover {
  //background-color: #ddd;
  background: linear-gradient(225deg, #ccc8c8, #f3eeee);
  box-shadow: -10px 10px 10px #cac6c6, 10px -10px 10px #fcf6f6;
  // animation-name: li_hover;
  // animation-duration: 0.5s;
  // animation-fill-mode: forwards;  //元素将保留由最后一个关键帧设置的样式值
}
li:hover button {
  background-color: palevioletred;
}
li > button:hover {
  color: white;
}
// @keyframes li_hover {
//     from {
//         height: 32px;
//     }
//     to {
//         height: 3em;
//     }
// }

@import url("./checkbox2.css");
</style>
