<template>
    <div class="todo-header">
        <input
            type="text"
            placeholder="输入要添加的内容"
            v-model="title"
            @keyup.enter="add"
        />
        <button @click="addFromServer">添加</button>
    </div>
</template>

<script>
import axios from "axios";
import {nanoid} from "nanoid";

export default {
    name: "MyHeader",
    data() {
        return {
            title: "",
        };
    },
    props: ["addTodo"],
    methods: {
        add(value) {
            this.title = this.title.trim();
            let todoObj = null;
            //校验数据
            if (!this.title) {
                //return alert("输入不能为空")
                todoObj = {
                    id: nanoid(),
                    title: value,
                    done: false,
                };
            } else {
                //将用户的输入包装成一个todo对象
                todoObj = { id: nanoid(), title: this.title, done: false };
            }
            //通App组件去添加一个todo对象
            this.addTodo(todoObj);
            //清空输入
            this.title = "";
        },
        addRandom() { //如果输入为空，则以随机id的前八位代替title内容
            this.add(nanoid().slice(0, 8))
        },
        addFromServer() { //如果输入为空，则以服务器api返回的字符串代替title内容
            axios.get('https://api.uixsj.cn/hitokoto/get?type=social').then(
                response => {
                    console.log(response.data);
                    this.add(response.data)
                },
                error => {
                    alert(error.message)
                }
            )
        },
    },
};
</script>

<style lang="less" scoped>
.todo-header {
    /* background-color: skyblue; */
    /* border:1px ridge skyblue; */
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;
    input {
        border: 1px solid lightgray;
        border-radius: 24px;
        font-size: 16px;
        color: purple;
        padding: 5px 15px 5px 15px;
        width: 200px;
        outline: none;
    }
    input:hover {
        box-shadow: 0 1px 6px rgb(32 33 36 / 28%);
        border-color: rgba(223,225,229,0);
    }
    button {
        background-color: #6dd5ed;
        //padding: 4px 12px 4px 12px;
        font-size: 14px;
        width: 70px;
        height: 30px;
        border: 0;
        border-radius: 16px;
        position: relative;
        margin-left: 10px;
        outline: none;
    }
    button:hover {
        //background-color: skyblue;
        color: white;
        background: linear-gradient(225deg, #2193b0, #6dd5ed);
        box-shadow: -3px 3px 3px #cac6c6, 3px -3px 3px #fcf6f6;
    }
}
</style>
